<template>
  <!--一定要给宽高，不然会报错，container没有高度-->
  <div id="chart">echarts百度地图</div>
</template>

<script>
export default {
  data() {
    return {};
  },
  computed: {
    points() {
      var boatIdArray = this.$store.state.line_points.map(item => {
        return item.boat_id;
      });
      var index = boatIdArray.indexOf(`${this.$store.state.selected_boat.id}`);
      if (index != -1) {
        return this.$store.state.line_points[index].points;
      } else {
        return [];
      }
    },
    watcher() {
      return JSON.stringify(this.$store.state.line_points);
    }
  },
  watch: {
    watcher(val, oldVal) {
      if(this.points[0].coords.length==1){
        return;
      }
      this.initMap();
    }
  },
  mounted() {
    this.initMap();
  },
  methods: {
    initMap() {
      var _this = this;
      let myChart = this.$echarts.init(document.getElementById("chart"));
      myChart.setOption({
        animation: false,
        bmap: {
          center: _this.$store.state.selected_boat.coord,
          zoom: 18,
          roam: true
        },
        series: [
          {
            type: "lines",
            coordinateSystem: "bmap",
            data: _this.points,
            polyline: true,
            lineStyle: {
              normal: {
                color: {
                  type: "linear",
                  x: 0,
                  y: 0,
                  x2: 0,
                  y2: 1,
                  colorStops: [
                    {
                      offset: 0,
                      color: "green" // 0% 处的颜色
                    },
                    {
                      offset: 1,
                      color: "green" // 100% 处的颜色
                    }
                  ],
                  global: false // 缺省为 false
                },
                opacity: 0.6,
                width: 3
              }
            }
          }
        ]
      });
    }
  }
};
</script>

<style>
#chart {
  width: 100%;
  height: 100%;
  /* width: 500px;
  height: 500px; */
  /* overflow: hidden !important; */
}
</style>