import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)
const state = {
	currentUser: "",
	isLogin: false,
	token: "",
	allData: [],
	boatmsg: [],
	selected_boat: {},
	drawer: false,
	beforeLeave_routerPath:null,
	websocket:null,
	line_points:[],
	line_data:[]
}
const getters = {

}
const mutations = {
	setUser(state, user) {
		if (user) {
			sessionStorage.setItem("userName", user.userName);
			sessionStorage.setItem("usertoken", user.userToken);
			state.currentUser = user.userName;
			state.isLogin = true;
			state.token = user.userToken;
		} else if (user == null) {
			sessionStorage.setItem("userName", null);
			sessionStorage.setItem("usertoken", null);
			state.currentUser = null;
			state.isLogin = false;
			state.token = "";
		}
	},
	getAllData(state, data) {
		state.allData = data;
	},
	getBoatmsg(state, data) {
		state.boatmsg = data;
		state.selected_boat = data[0];
	},
	setSelected_boat(state, boat) {
		state.selected_boat = boat;
	},
	openDrawer(state) {
		state.drawer = true;
	},
	closeDrawer(state) {
		state.drawer = false;
	},
	setRouter(state,routerPath){
		state.beforeLeave_routerPath = routerPath;
	},
	setWebSocket(state,websock){
		state.websocket=websock;
	},
	closeSocket(state){
		state.websocket.close();
		state.websocket=null;
	},
	setPoints(state,points){
		var boatIdArray = state.line_points.map((item)=>{
			return item.boat_id;
		})
		var index = boatIdArray.indexOf(points[0].boat_id);
		if(index!=-1){
			state.line_points[index].points[0].coords = state.line_points[index].points[0].coords.concat(points[0].coords);
			
		}else{
			var obj={
				boat_id:points[0].boat_id,
				points:[{
					coords: points[0].coords
				}]
			}
			state.line_points.push(obj);
		}
	},
	setLineData(state, singleLine_data) {
		var boatIdArray = state.line_data.map((item) => {
			return item.boat_id;
		})
		var index = boatIdArray.indexOf(singleLine_data.boat_id);
		if (index != -1) {
			state.line_data[index].dateTime = state.line_data[index].dateTime.concat(singleLine_data.dateTime);
			state.line_data[index].pH = state.line_data[index].pH.concat(singleLine_data.pH);
		} else {
			var obj = {
				boat_id: singleLine_data.boat_id,
				dateTime: [singleLine_data.dateTime],
				pH: [singleLine_data.pH],
			}
			state.line_data.push(obj);
		}
	}
}
const actions = {
	setAllData({
		commit
	}) {
		axios.get("/api/getAllData").then(data => {
			commit("getAllData", data.data.data);
			commit("getBoatmsg", data.data.boatmsg);
		})
	}
}

export const store = new Vuex.Store({
		state,
		getters,
		mutations,
		actions
	}

);