import Vue from 'vue'
import Router from 'vue-router'
const Index = () =>
  import('./views/index/Index.vue')
const Userpage = () =>
  import('./views/content/Userpage.vue')
const UserpageChild = () =>
  import('./views/content/UserpageChild.vue')
const Myboat = () =>
  import('./views/content/Myboat.vue')
const Datapage = () =>
  import('./views/content/Datapage.vue')
const Controlpage = () =>
  import('./views/content/Controlpage.vue')
const Leave = () =>
  import('./views/index/Leave.vue')
const UserMsg = () =>
  import('./views/content/UserMsg.vue')
const NewBoat = () =>
  import('./views/content/NewBoat.vue')
// import Index from './components/index/Index.vue'
// import Userpage from './components/content/Userpage.vue'
// import UserpageChild from './components/content/UserpageChild.vue'
// import Myboat from './components/content/Myboat.vue'
// import Datapage from './components/content/Datapage.vue'
// import Controlpage from './components/content/Controlpage.vue'
Router.prototype.goBack = function () {
  this.isBack = true
  window.history.go(-1)
}
Vue.use(Router)

export default new Router({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes: [{
      path: '/',
      redirect: '/index'
    },
    {
      path: '/index',
      component: Index
    },
    {
      path: '/leave',
      component: Leave
    },
    {
      path: '/userpage',
      component: Userpage,
      children: [{
          path: "/",
          redirect: 'firstpage'
        },
        {
          path: 'firstpage',
          component: UserpageChild
        },
        {
          path: 'myboat',
          component: Myboat
        },
        {
          path: 'datapage',
          component: Datapage
        },
        {
          path: 'controlpage',
          component: Controlpage
        },
        {
          path: 'usermsg',
          component: UserMsg
        },
        {
          path: 'newboat',
          component: NewBoat
        },
        {
          path: '*',
          redirect: 'firstpage'
        },
      ]
    },
    {
      path: '*',
      redirect: '/index'
    },
  ]
})